@-webkit-keyframes open {
  0% { margin-top:0px; opacity: 0.5; }
  100% { margin-top:40px; opacity: 1; }
}

@keyframes open {
  0% { margin-top:0px; opacity: 0.5; }
  100% { margin-top:40px; opacity: 1; }
}

@-webkit-keyframes close {
  0% { margin-top:40px; opacity: 1; }
  100% { margin-top:0px; opacity: 0; }
}

@keyframes close {
  0% { margin-top:40px; opacity: 1; }
  100% { margin-top:0px; opacity: 0; }
}


